<template>
	<div class="main">
		<header-vue title=''></header-vue>
		<div class="mainContent">
			<!--  -->
			<van-swipe class="my-swipe"  indicator-color="#fc894c" :autoplay="4000">
				<van-swipe-item v-for="(image,index) in imgList" :key="image.categoryId" >
					<a href="#" @click="swiperGo(image.id)"><img :src="swiperArrs[index].url"></a>
				</van-swipe-item>
			</van-swipe>
			<!--  -->
			<div class="recommend">
				<p></p>

				<van-card :key="objs.id" v-for="objs in goodList" :price="(objs.salePrice/100)|postNum" :title="objs.productName" :thumb="objs.thumbnailImgUrl" @click="goDetails(objs.id)">
					<template #desc>
						<van-tag plain type="default">{{objs.productSubtitle}}</van-tag>
						<van-tag plain type="default">{{objs.intro}}</van-tag>
					</template>

					<template #tags>
						<div>
							<!-- <van-tag plain type="default" class="yunfei">{{objs.transportPrice == 0?'':objs.transportPrice}}</van-tag> -->
						</div>
					</template>
				</van-card>

			</div>

		</div>
		<footer-vue></footer-vue>
	</div>
</template>

<script>
    import unils from "../../assets/js/common.js"
	import Header from '../../components/header'
	import Footer from '../../components/footer'
	export default {
		name: "home",
		data() {
			return {
				isType: 1,
				imgList: [],
				bannerlist: [],
				goodList:[],
				swiperArrs:[]

			}
		},
		components: {
			headerVue: Header,
			footerVue: Footer
		},
		methods:{
			goDetails(id){
				this.$router.push({
					name:'productdetail',
					params:{
						id:id
					}
				})
				//ID
				sessionStorage.setItem('proId',id);
			},
			swiperGo(id){
				this.goDetails(id);
			},
			getUrlParam(name){
				var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
				let url = window.location.href.split('#')[0]
				let search = url.split('?')[1]
				if (search) {
					var r = search.substr(0).match(reg)
					if (r !== null) return unescape(r[2])
					return null
				} else {
					return null
				}
			}
		},
		filters:{
			postNum(value){
               return unils.hasDot(value);
			}

		},
		created(){
			//
			this.$get('/client/product/list',{
               isRecommend:true
			}).then((res)=> {
				if(res.data.status == 0){
				   this.goodList = res.data.data.list;
				   var swiperArr = this.goodList.slice(0,3);
				   this.imgList = swiperArr;
				   var imgArr = [];
				   swiperArr.forEach(function(item){
					   imgArr.push(JSON.parse(item.coverImgUrl)[0]);
				   })
				   this.swiperArrs = imgArr;
				   
				}

			})
		},
		
	}
</script>

<style scoped>
.mainContent .van-hairline--surround::after {
		border: none;
	}
.recommend>p{
	font-size: 30px;
	margin: 10px 0px;
	padding: 15px 15px 15px 30px;
}
/*  */
img {
	width: 100%;
	height: 100%;
}
.mainContent{
	margin-top: 100px;
	margin-bottom: 100px;
}
.recommend{
	margin: 15px;
	border-radius: 10px;
	background-color: white;
}
.main{
	background-color: #f6f6f6;
}
.yunfei{
	color: #fb6003;
}
.my-swipe{
	margin-left: 15px;
	margin-right: 15px;
}
.van-card__title {
	font-size: 13px;
}
.van-tag{
	font-size: 11px;
}
</style>
