<template>
	<!--  -->
	<div class="ordersParts">
		<header-vue title='' isDetail=1></header-vue>
		<div class="orderBottom">
			<p class="danTitle"></p>
			<!--  -->
			<div v-if="allOrderList.length == 0" class="emptyOredr">
				
			</div>
			<div v-if="allOrderList.length > 0">
				<div class="everyOrder" v-for="items in allOrderList" :key="items.id">
					<div class="orderTitles">
						<span>:</span>
						<span>{{items.orderCode}}</span>
						<span class="orderStatus">{{items.orderStatusStr}}</span>
					</div>
					<div class="goodsWrap" v-for="goods in items.goodsVOList" :key="goods.attrId">
						<van-card :num="goods.count" :price="goods.price" :title="goods.productName" :thumb="goods.thumbnailImgUrl">
							<template #tags>
								<van-tag plain type="default">{{goods.productAttr}}</van-tag>
							</template>
						</van-card>
					</div>
					<!--  101-->
					<div class="bottomParts">
						<van-button @click="goDetail(items)" round size="small"></van-button>
						<van-button @click="payMoney(items)" round class="zhifuBtn" size="small"></van-button>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>
<script>
	import Header from '../../components/header'
	import wx from 'weixin-js-sdk'
	export default {
		data() {
			return {
				allOrderList: [],
				saveallObj:''
			}
		},
		components: {
			headerVue: Header,
		},
		methods: {
			goAfterSale() {
				this.$router.push("/ui/aftersaleapply")
			},
			goDetail(detailData) {
				this.$router.push({
					name: 'orderDetail',
					params: {
						detailData
					}
				})
			},
			payMoney(detail) {
				this.$postImg('/order/getJsSdkConfig', {
					url:location.href
				}).then(res => {
					var configObj = res.data.data;
					this.saveallObj = configObj;
					let {debug,appId,timestamp,nonceStr,signature,jsApiList} = this.saveallObj;
						wx.config({
							debug: false, // ,apialertpclogpc
							appId: appId, // 
							timestamp: timestamp, // 
							nonceStr: nonceStr, // 
							signature: signature, // 
							jsApiList: jsApiList // JS
						});
						//
						wx.ready(function(res) {
							console.log("wx.ready-jssdk---");
						});
				})

				var orderObj = {
					orderCode: detail.orderCode,
					productId: detail.goodsVOList[0].productId,
					productNormCode: detail.goodsVOList[0].productNormCode || '',
					productNormId: detail.goodsVOList[0].productNormId,
					totalFee: detail.totalPrice
				}
				this.$post('/order/pay', orderObj).then(res => {
					
					if (res.data.status == 0) {
						var getWxabout = res.data.data;
						wx.chooseWXPay({
							timestamp: getWxabout.timeStamp,
							nonceStr: getWxabout.nonceStr,
							package: getWxabout.wxPackage,
							signType: getWxabout.signType,
							paySign: getWxabout.paySign,
							success: function(res) {
								alert(res)
							},
							error: function(err) {
								alert(err)
							}
						});

					}
				})

			}
		},
		created() { //
			this.$get('/order').then((res) => {
				if (res.data.status == 0) {
					var searchData = res.data.data;
					var fileArr = [];
					searchData.forEach(function(item) {
						if (item.orderStatus == 101) {
							fileArr.push(item);
						}
					})
					this.allOrderList = fileArr;
				} else {
					this.$toast("")
				}
			})
			//
			

		}
	}
</script>

<style scoped>
	.ordersParts .van-hairline--surround::after {
		border: none;
	}

	.danTitle {
		font-size: 32px;
		color: black;
		padding-left: 20px;
		background: white;
		height: 80px;
		line-height: 80px;
	}

	.orderBottom {
		margin-top: 110px;
	}

	.orderTitles {
		font-size: 30px;
		display: flex;
		justify-content: space-around;
		padding-top: 20px;
		padding-bottom: 10px;
		align-items: center;
	}

	.everyOrder {
		background-color: white;
		margin-bottom: 15px;
	}

	.bottomParts {
		text-align: center;
		border-top: 1px solid #e4e4e4;
		display: flex;
		justify-content: flex-end;
		padding: 25px 0px;
	}

	.bottomParts .van-button {
		width: 80px;
		margin-right: 5px;
	}

	.orderStatus {
		color: #f97c34;
	}

	.zhifuBtn {
		color: #f97c34;
		border-color: #f97c34;
	}

	.van-card__title {
		font-size: 14px;
		padding-bottom: 5px;
	}

	.van-card__num {
		color: black;
		font-size: 14px;
	}

	.emptyOredr {
		text-align: center;
		font-size: 30px;
	}
</style>
