<template>
	<div class="ordersParts">
		<header-vue title='' isDetail=1></header-vue>
		<div class="orderBottom">
			<p class="danTitle"></p>
			<div class="everyOrder" v-for="items in allOrderList" :key="items.id">
				<div class="orderTitles">
					<span>:</span>
					<span>{{items.orderCode}}</span>
					<span class="orderStatus">{{items.orderStatusStr}}</span>
				</div>
				<div class="goodsWrap" v-for="goods in items.goodsVOList" :key="goods.attrId">
					<van-card :num="goods.count" :price="goods.price" :title="goods.productName" :thumb="goods.thumbnailImgUrl">
						<template #tags>
							<van-tag plain type="default">{{goods.productAttr}}</van-tag>
						</template>
					</van-card>
				</div>
				<!--  101-->
				<div class="bottomParts" v-if="items.orderStatus == 101">
					<van-button   @click="goDetail(items)" round size="small"></van-button>
					<van-button   @click="goLogisti" round class="zhifuBtn" size="small"></van-button> 
				</div>
				<!--  303-->
				<div class="bottomParts" v-if="items.orderStatus == 303">
					<van-button   @click="goLogisti" round size="small"></van-button>
				</div>
				<!--  401-->
				<div class="bottomParts" v-if="items.orderStatus == 401">
					<van-button   @click="goDetail(items)" round size="small"></van-button>
				</div>
				<!--  305-->
				<div class="bottomParts" v-if="items.orderStatus == 305">
					<van-button   @click="goDetail(items)" round size="small"></van-button>
					<van-button   @click="goAfterSale" round size="small"></van-button>
				</div>
				<!--  102-->
				<div class="bottomParts" v-if="items.orderStatus == 102">
					<van-button   @click="goDetail(items)" round size="small"></van-button>
				</div>
				
			</div>
		</div>
	</div>
</template>
<script>
	import Header from '../../components/header'
	export default {
		data() {
			return {
				allOrderList:[],
			}
		},
		components: {
			headerVue: Header,
		},
		methods: {
			goLogisti() {

			},
			goAfterSale() {
				this.$router.push("/aftersaleapply")
			},
			goDetail(detailData) {
				this.$router.push({
					name:'orderDetail',
					params:{
						detailData
					}
				})
			}
		},
		created(){
            //
			this.$get('/order').then((res)=> {
				if(res.data.status == 0){
				  this.allOrderList = res.data.data;
				}else{
					this.$toast("")
				}

			})
			

		}
	}
</script>

<style scoped>
	.ordersParts .van-hairline--surround::after {
		border: none;
	}

	.danTitle {
		font-size: 32px;
		color: black;
		padding-left: 20px;
		background: white;
		height: 80px;
		line-height: 80px;
	}

	.orderBottom {
		margin-top: 110px;
	}

	.orderTitles {
		font-size: 30px;
		display: flex;
		justify-content: space-around;
		padding-top: 20px;
		padding-bottom: 10px;
		align-items: center;
	}
	.everyOrder{
		background-color: white;
		margin-bottom: 15px;
	}
	.bottomParts{
	 text-align: center;
	 border-top: 1px solid #e4e4e4;
	 display: flex;
	 justify-content: flex-end;
	 padding: 25px 0px;
	}
	.bottomParts .van-button{
		width: 80px;
		margin-right: 5px;
	}
	.orderStatus{
		color: #f97c34;
	}
	.zhifuBtn{
		color:#f97c34 ;
		border-color: #f97c34;
	}
	.van-card__title{
		font-size: 14px;
	    padding-bottom: 5px;
	}
	.van-card__num{
		color: black;
		font-size: 14px;
	}
</style>
