<template>
	<div class="orderDetailwraps">
		<header-vue title='' isDetail=1></header-vue>
		<div class="firstWrap">
			<ul class="topPart">
				<li>
					<span class="specialShow">:<strong>{{productList.orderCode}}</strong></span>
					<span>:<strong>{{productList.orderTime}}</strong></span>
				</li>
				<li class="orderStatus">
					{{productList.orderStatusStr}}
				</li>
			</ul>
			<ul class="middlePart">
				<li>
					<span class="specialShow">:<strong>{{productList.mailName}}</strong></span>
					<span>{{productList.address}}</span>
				</li>
				<li>
					{{productList.phone}}
				</li>
			</ul>
			<div class="goodsWrapdetail">
				<van-card :num="items.count" :price="items.price" :title="items.productName" :thumb="items.thumbnailImgUrl" v-for="items in productList.goodsVOList" :key="items.attrId">
					<template #tags>
						<van-tag plain type="default">{{items.productAttr}}</van-tag>
					</template>
				</van-card>
			</div>
			<ul class="bottomPart">
				<li><span></span><strong>{{productList.payPrice}}</strong></li>
				<li><span></span><strong>{{productList.mailCost}}</strong></li>
				<li><span></span><strong>{{productList.totalPrice}}</strong></li>
			</ul>
			<!-- -->
			<div class="allBtns">
                <van-button round size="large" @click="payMoney" class="zhifuBtn"></van-button>
			</div>
		</div>
	</div>
</template>
<script>
	import Header from '../../components/header'
	export default {
		data() {
			return {
				goodList: [{
					id: 1,
					price: '258.00',
					title: '(EDASDASD) ALSDAS',
					thumb: 'https://img.yzcdn.cn/vant/ipad.jpeg',
					desc1: '',
					desc2: '',
                }],
                productList:[]
			}
		},
		methods: {
			payMoney(){//
			    console.log(this.productList);
				var orderObj = {
					orderCode:this.productList.orderCode,
					productId:'',
					productNormCode:'',
					productNormId:'',
					totalFee:this.productList.totalPrice
				}
				this.$post('/order/pay',orderObj).then((res) => {
					if (res.data.status == 0) {
                        console.log(res);
					} 
				})
            }
		},
		components: {
			headerVue: Header,
		},
		created() {//
			var getParams = this.$route.params;
            var getCode = getParams.orderCode;
            this.$get('admin/order/code', {
				orderCode: getCode
			}).then(res=>{
				this.productList = res.data.data;
				
            })

		}

	}
</script>
