// 
function hasDot(num){
    if(!isNaN(num)){
        return ( (num + '').indexOf('.') != -1 ) ? num.toFixed(2) : num.toFixed(2);   
    }
}
//
function setZero(value){
	return value < 10 ? "0"+value : value;
}
function formatTime(num) {
		let len = (num + ' ').length;
		let data = "";
		if (len == 10) {
		   data = num * 1000;
		} else {//13,,
		   data = num;
		}
		var dates = new Date(data); //,====> : Fri Apr 19 2019 13:42:34 GMT+0800 ()
		let Y = dates.getFullYear() + "-";
		let M = (dates.getMonth() + 1 < 10 ? "0" + (dates.getMonth() + 1) : dates.getMonth() + 1) + "-";
		let D = setZero(dates.getDate()) + " ";
		let h = setZero(dates.getHours()) + ':';
		let m = setZero(dates.getMinutes()) + ':';
		let s = setZero(dates.getSeconds());
		return Y + M + D + h + m + s;
}
//
function isWeixin() {
    var ua = window.navigator.userAgent.toLowerCase();
    if(ua.match(/MicroMessenger/i) == 'micromessenger'){
        return true;
    }
    return false;
}
//
function isAlipay() {
    var ua = window.navigator.userAgent.toLowerCase();
    if(ua.match(/Alipay/i) == "alipay"){
        return true;
    }
    return false;
}

export default{
    hasDot,
    formatTime,
    isWeixin,
    isAlipay
}

