<template>
	<div class="mainPart">
		<header-vue title=''></header-vue>
		<div class="partContent">
			<div class="partSearch">
				<van-search  v-model="value" shape="round" placeholder="" :clearable='isClear' @focus="focusJump" />
			</div>
			<div class="partBox">
				<div>
					<van-sidebar v-model="activeKey" @change="onChange">
						<van-sidebar-item :title="item.categoryName" v-for="item in typeArr" :key="item.id"/>
					</van-sidebar>
				</div>

				<div class="goodsWrap">
					<van-card :price="(items.salePrice/100) | postNum" :title="items.productName" :thumb="items.thumbnailImgUrl" v-for="items in goodList" :key="items.id" @click="goDetails(items.id)">
						<template #tags>
							<van-tag plain type="default">{{items.intro}}</van-tag>
							<!-- <van-tag plain type="default">{{items.desc2}}</van-tag> -->
						</template>
						<template #num>
						 <van-icon class="iconfont" class-prefix='icon' name='tianjia' size="18" />
					    </template>
					</van-card>

				</div>
			</div>

		</div>
		<footer-vue></footer-vue>
	</div>
</template>
<script>
    import unils from "../../assets/js/common.js"
	import Header from '../../components/header'
	import Footer from '../../components/footer'
	export default {
		data() {
			return {
				isType: 2,
				value: '',
				activeKey: 0,
				isClear: false,
				typeArr:[],
				goodList:[]
			}
		},
		methods: {
			onChange(index) {
				var clickId = this.typeArr[index].id;
				this.$get('/client/product/list',{
						categoryId:clickId
					}).then(res=>{
						this.goodList = res.data.data.list;
					})
			},
			focusJump() {
				this.$router.push('/ui/searchpage')
			},
			goDetails(id){
				this.$router.push({
					name:'productdetail',
					params:{
						id:id
					}
				})
				//ID
				sessionStorage.setItem('proId',id);
			}
		},
		components: {
			headerVue: Header,
			footerVue: Footer
		},
		filters:{
			postNum(value){
               return unils.hasDot(value);
			}

		},
		created(){
			//
			this.$get('/client/product/categoryList').then((response)=> {
				if(response.data.status == 0){
					this.typeArr = response.data.data;
					var firstTypeId = this.typeArr[0].id;
				//
				this.$get('/client/product/list',{
						categoryId:firstTypeId
					}).then(res=>{
						this.goodList = res.data.data.list;
					})
					
				}else{
					this.$toast.fail('');
				}
				
            })
            
		}
	}
</script>

<style scoped>
.icon-tianjia{
	color: #f97c34;
}
.partContent{
	margin-top: 100px;
}
	.partBox {
		display: flex;
		margin: 15px 10px 100px 0px;
	}
	/*  */
	.partContent .van-hairline--surround::after {
		border: none;
	}
	.goodsWrap {
		flex: 1;
	}
	.van-card__title {
	font-size: 13px;
}
</style>
