<template>
	<div class="mineMain">
		<header-vue title=''></header-vue>
		<div class="mine-content">
			<div class="mine-login">
					<van-icon name="user-circle-o" />
				<!-- <button @click="login" :disabled="isDisabled">{{initText}}</button> -->
			</div>
			<div class="myOrder">
				<p class="newOrder"></p>
				<van-cell title="" is-link  @click="goAll">
					<template #icon>
						<img src="../../assets/images/quanbudingdan.png" alt="" class="iconClass">
					</template>
				</van-cell>
				<van-cell title="" is-link   @click="goWaitpay">
				<template #icon>
					<img src="../../assets/images/daifukuan.png" alt="" class="iconClass">
					</template>

				</van-cell>
				<van-cell title="" is-link   @click="goWaitreceive">
				<template #icon>
					<img src="../../assets/images/daishouhuo.png" alt="" class="iconClass">
					</template>
				</van-cell>
				<van-cell title="/" is-link  @click="gorefund">
				<template #icon>
					<img src="../../assets/images/tuikuan.png" alt="" class="iconClass">
					</template>
				</van-cell>
				<div class="adressBox">
					<van-cell title="" is-link   @click="goAdress">
					<template #icon>
						<img src="../../assets/images/dizhi.png" alt="" class="iconClass">
						</template>
					</van-cell>
				</div>
				
			</div>

		</div>
		<!--  -->
		<van-dialog v-model="isshow" title="" show-cancel-button confirmButtonText="" @confirm="loginIn" :before-close="isClosed">
			<div class="loginWrap">
				<van-field v-model="uesrName" label="" />
				<van-field v-model="password" type="password" label="" />
		   </div>
		</van-dialog>

		<!-- -->
		<footer-vue></footer-vue>

	</div>
</template>
<script>
	import Header from '../../components/header'
	import Footer from '../../components/footer'
	export default {
		data() {
			return {
				isType: 3,
				initText:'',
				isDisabled:false,
				isshow:false,
				uesrName:'',
				password:''
			}
		},
		methods: {
			gorefund(){
				this.$router.push({
					name:'refund',
				})
			},
			goAll(){
				 this.$router.push({
					name:'allOrder',
				})
			},
			goWaitpay(){
                 this.$router.push({
					name:'waitpay',
				})
			},
			goWaitreceive(){
                 this.$router.push({
					name:'waitreceive',
				})
			},
			goAdress(){
				 this.$router.push({
					name:'singleadress',
				})
			},
			login() {
				this.isshow = true;
			},
			loginIn(){
				console.log('sure');
			},
			isClosed(action, done){
				if(action === 'confirm') {
					setTimeout(done, 1000)
					if(!this.uesrName || !this.password) {
						this.$toast("")
						done(false)
					}
				} else if(action === 'cancel') {
					done() //
				}
			}
		},
		components: {
			headerVue: Header,
			footerVue: Footer
		},
		created(){//this.$store.state
			let getUsers = this.$store.state.userInfo;
			if(getUsers!='' && getUsers!= null){
				this.initText = getUsers.uName;
				this.isDisabled = true;
			}else{
				this.initText = '';
				this.isDisabled = false;
			}

		}

	}
</script>

<style scoped>
.adressBox{
	margin-top: 10px;
}
.mine-content{
	margin-top: 100px;
}
.mine-login{
	display: flex;
	align-items: center;
	padding-left: 10px;
	padding-top: 7px;
}
.mine-login>button{
	font-size: 35px;
	padding-left: 15px;
	height: 60px;
	text-align: left;
    border: none;
    background-color: #f6f6f6;
}
.mine-login .van-icon-user-circle-o{
	color: #cccccc;
}
.newOrder{
	font-size: 30px;
	padding-left: 35.5px;
	background: white;
	padding-top: 25px;
	padding-bottom: 25px;
	margin-bottom: 10px;
	margin-top: 10px;
}
.iconClass{
	width: 38px;
	height: 38px;
}
.van-cell--clickable{
    align-items: center;
}
</style>
