<template>
	<div class="adressBox">
		<header-vue :title='title' isDetail=1></header-vue>
		<ul>
			<li>
				<van-field v-model="peopleName" label="" placeholder="" />
			</li>
			<li>
				<van-field v-model="peopleTele" type="tel" label="" placeholder="" />
			</li>
			<li class="addWrap">
				<van-field label="" :value="defaultValue" right-icon="arrow" readonly @click="handel" />
			</li>
			<li>
				<van-field v-model="message" rows="2" autosize label="" type="textarea" placeholder="" />
			</li>
		</ul>

		<van-popup v-model="showPicker" round position="bottom">
			<van-area @confirm="onConfirm"  title="" :area-list="areaList" :value="showCode"/>
         </van-popup>

		 <div class="btnWraps">
			 <van-button round type="info" @click="deleAdress" v-if="isShow"></van-button>
			 <van-button round  @click="saveAdress"></van-button>
		 </div>

	</div>
</template>
<script>
	import Header from '../../components/header'
	import areaData from '../../modules/area.js';
	export default {
		data() {
			return {
				title: '',
				text: '',
				message: '',
				showPicker:false,
				columns:'',
				peopleName:'',
				peopleTele:'',
				defaultValue:'',
				receiveData:'',
				areaList:areaData,
				isShow:false,
				showCode:''
			}
		},
		components: {
			headerVue: Header,
		},
		methods: {
			handel() {
				this.showPicker = true;
			},
			onConfirm(prodata) {
				var pinArr = [];
				prodata.forEach(function(items){
					pinArr.push(items.name)
				})
				var lastItems = prodata.pop();//
				//
				this.defaultValue = pinArr.join("/");
				this.saveAllProvince = pinArr;
				this.showPicker = false;
				this.saveLastItem = lastItems;
			},
			saveAdress(){
				if(this.peopleName == "" || this.peopleTele == "" ||this.message == ""){
					this.$toast("");
					return;
				}
				//
				var province,city,district,postCode;
				if(this.saveAllProvince){
					if(this.saveAllProvince.length>0){
						province = this.saveAllProvince[0];
						city = this.saveAllProvince[1];
						district = this.saveAllProvince[2];
						postCode = this.saveLastItem.code;
					}
				}else{//
					province = this.backArr[0];
					city = this.backArr[1];
					district = this.backArr[2];
					postCode = this.receiveData.code;
				}
				var adressInfo = {
					name:this.peopleName,
					phone:this.peopleTele,
					detail:this.message,
					province,
					city,
					district,
					code:postCode
				}

				if(this.saveFlag != 1){//
				   adressInfo.id = this.receiveData.id;
				}
				this.$post('/address/save',adressInfo).then((res) => {
					if (res.data.status == 0) {
				       this.$toast("")
					} 
				})
			},
			deleAdress(){
				var deleObj = this.receiveData;
				deleObj.status = 0;
				this.$post('/address/update',deleObj).then((res) => {
					if (res.data.status == 0) {
					   this.$toast("")
					} 
				})
			}
		},
		created() {
			let getFlag = this.$route.params.flag;
			let getPostdata = this.$route.params.postdata;
			this.saveFlag = getFlag;
			//
			if (getFlag == 1) {
				this.title = "";
			} else if (getFlag == 2) {//
				this.title = "";
				this.isShow = true;
				this.receiveData = getPostdata;
				this.peopleName = getPostdata.name;
				this.peopleTele = getPostdata.phone;
				this.message = getPostdata.address;
				this.showCode = getPostdata.code;
				//
				var reg = /.+?(|||||)/g;
				var regArr = getPostdata.address.match(reg);
				this.backArr = regArr;
				this.defaultValue = regArr.join("/");
			}
		}
	}
</script>
<style scoped>
	.adressBox .van-address-edit {
		margin-top: 45px;
	}

	.adressBox .van-address-edit__buttons {
		display: flex;
		flex-direction: row-reverse;
	}

	.adressBox>ul {
		display: flex;
		flex-direction: column;
	}

	.adressBox {
		margin-top: 100px;
	}
	.addWrap>span:nth-of-type(2) {
		color: #666666;
		width: 253px;
	}

	.addWrap>span:nth-of-type(1) {
		padding-right: 75px;
	}

	.addWrap>span:nth-of-type(3) {
		margin-top: 10px;
	}
	.btnWraps{
		text-align: center;
		width: 100%;
		display: flex;
		margin-top: 30px;
		background-color: white;
		padding: 15px 0px;
	}
	.btnWraps .van-button--default{
		background: linear-gradient(to right,#f97c34,#f97c34);
        color: white;
	}
	.btnWraps .van-button--info{
      background-color: #f6f6f6;
	  color: black;
	  border-color: #f6f6f6;
	}
	.btnWraps button{
		flex: 1;
		margin: 0px 10px;
	}
</style>
