import Home from '../views/homes/home';
import Mine from '../views/mines/mine';
import Part from '../views/parts/part';
import ProductDetail from '../views/parts/productDetail';
import Searchpage  from '../views/parts/searchpage';
import ConfirmOrder from '../views/parts/confirmOrder';
import EditAdress  from '../views/parts/editAdress';
import LogisticDetail from '../views/mines/logisticDetail'; //
import AftersaleApply  from '../views/mines/aftersaleApply'; //
import AllAdress  from '../views/parts/allAdress'; //
import OrderDetail  from '../views/mines/orderDetail'; //
import Login  from '../views/login';
import SingleAdress  from '../views/parts/singleAdress';
import AllOrder  from '../views/mines/allorders'; //
import Refund   from '../views/mines/refund' ; ///
import WaitPay   from '../views/mines/waitpay' ; //
import WaitReceive   from '../views/mines/waitreceive' ; //
import Author  from '../views/homes/author';
import Payorder  from  '../views/mines/payorder'; 


const routes = [{
		path: '/',
		redirect: '/ui/home'
	},
	{
		path: '/ui/home',
		name: 'home',
		component: Home
	},
	{
		path: '/ui/mine',
		name: 'mine',
		component: Mine
	}, {
		path: '/ui/part',
		name: 'part',
		component: Part
	},
	{
		path: '/ui/searchpage',
		name: 'searchpage',
		component: Searchpage
	},
	{
		path: '/ui/productdetail',
		name: 'productdetail',
		component: ProductDetail
	},
	{
		path: '/ui/confirmOrder',
		name: 'confirmOrder',
		component: ConfirmOrder
	},
	{
		path: '/ui/editAdress',
		name: 'editAdress',
		component: EditAdress
	},
	{
		path: '/ui/aftersaleapply',
		name: 'aftersaleapply',
		component: AftersaleApply
	},
	{
		path: '/ui/logisticdetail',
		name: 'logisticdetail',
		component: LogisticDetail
	},
	{
		path: '/ui/allAdress',
		name: 'allAdress',
		component: AllAdress
	},
	{
		path: '/ui/allOrder',
		name: 'allOrder',
		component: AllOrder
	},
	{
		path: '/ui/orderDetail',
		name: 'orderDetail',
		component: OrderDetail
	},{
		path: '/ui/login',
		name: 'login',
		component: Login
	},
	{
		path: '/ui/singleadress',
		name: 'singleadress',
		component: SingleAdress
	},
	{
		path: '/ui/refund',
		name: 'refund',
		component: Refund
	},
	{
		path: '/ui/waitpay',
		name: 'waitpay',
		component: WaitPay
	},
	{
		path: '/ui/waitreceive',
		name: 'waitreceive',
		component: WaitReceive
	},
	{
		path: '/ui/author',
		name: 'author',
		component: Author
	},
	{
		path: '/ui/payorder',
		name: 'payorder',
		component: Payorder
	}
];

const router = new VueRouter({
	mode:'history',
	base:'/busi/wx',
	routes,
});
// 
router.beforeEach((to, from, next) => {
	let isToken = window.sessionStorage.getItem('token');
	if (!isToken && to.path != "/ui/author") {
		window.sessionStorage.setItem("beforeLoginUrl", to.fullPath); // url
		// next("/ui/author");
		// return false;
		next()
		
	}else{
		next()
	}
});

export default router
