<template>
	<div class="loginWrap">
		<van-field v-model="text" label="" />
		<van-field v-model="password" type="password" label="" />
        <van-button  size="large" @click="loginIn" round ></van-button>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				text: '',
				password: '',
			}
		},
		methods:{
			loginIn(){
				let getUser = {
					uName:this.text,
					passWord:this.password
				};
				this.isDisable = true;
				this.$store.commit('saveUserInfo',getUser);
				this.$router.push("/home")
			}
		},
		created(){
		}
	}
</script>

<style scoped>
	.loginWrap {
        margin-top: 20px;
	}
	.loginWrap .van-button--default{
		background: linear-gradient(to right,#fe9930,#f7631f);
    color: white;
	}
</style>
