<template>
	<div class="applyWrap">
		<header-vue title='' isDetail='1'></header-vue>
        <div class="imgSubmit">
            <van-cell-group>
                <van-field v-model="message" rows="2" :autosize="textareaObj" type="textarea" maxlength="150" placeholder="" show-word-limit />
            </van-cell-group>
            <div class="uploadArea">
                <van-uploader v-model="fileList" multiple :max-count="3" upload-text="" :after-read="afterRead"  :deletable="isdelete" />
                <span><br>
                (3)
                </span>
            </div>
            <div class="btnWraps">
            <van-button  size="large" round @click="handleSubmit"></van-button>
            </div>
         </div>
	</div>
</template>
<script>
	import Header from '../../components/header'
	export default {
		data() {
			return {
				message: '',
                fileList: [],
                textareaObj:{
                    maxHeight: 350,
                    minHeight: 220 
                },
                uploadImages:[],
                isdelete:false,
                saveData:''
			}
		},
		components: {
			headerVue: Header,
        },
        methods:{
            afterRead(file){
                var saveThis = this;
                var xhr, formData;
                let queryDetailKey = "https://tchopshop.weein.cn/busi/admin/uploadFile/img";
                xhr = new XMLHttpRequest();
                xhr.withCredentials = true;
                xhr.open("POST", queryDetailKey);
                formData = new FormData();
                formData.append("file", file.file);
                xhr.onload = function(e){
                    var json;
                    if (xhr.status != 200) {
                        failure('HTTP Error: ' + xhr.status);
                        return;
                    }
                    json = JSON.parse(this.responseText);
                    //
                    saveThis.uploadImages.push(json.data);
                    console.log(saveThis.uploadImages)
                };
                xhr.send(formData);
            },
            handleSubmit(){
                var afterObj = {};
                if(this.message == ''){
                    this.$toast("");
                }else{
                    var afterObj = {
                        orderCode:this.saveData.orderCode,
                        refundFee:(this.saveData.payPrice)*100,
                        reason:this.message,
                        voucher:this.uploadImages.join(",")
                    };
                    this.$post('/order/applyReturn',afterObj).then((res) => {
                        if (res.data.status == 0) {
                            this.$toast("");
                            this.uploadImages = [];
                            // this.fileList = [];
                        } 
                    })
                }
            }
        },
        created(){
            var getParams = this.$route.params;
            this.saveData = getParams.detailData;
        }
	}
</script>
<style scoped>
.imgSubmit{
    margin-top: 110px;
}
.van-cell-group{
    margin: 10px;
}
.uploadArea{
    background-color: white;
    margin: 10px 20px;
    display: flex;
    align-items: center;
    padding-top: 20px;
}
.uploadArea>span{
    font-size: 27px;
}
.van-button--default{
   color: white;
   background: linear-gradient(to right,#fe9930,#f7631f);
}


</style>
